<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="false" :listType="['List']" ModuleName="Asset" SubModuleCode="Asset"
            :PageSize="PageSize" IdentityColumn="ASSET_CATALOGUE_ID" HeaderText="Check-Out List" :ListData="ProductData"
            :HeaderData="Headers" :widgets="widgets" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions"
            :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray" :SortExp="SortExp" :SortBy="SortBy"
            v-on:LeftsearchButtonClick="leftCommonSearch" :NorecordfoundText="$t('NoRecordfound')"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">

            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'Status'">

                    <span v-if="data.row.StatusName == 'Active'" class="px-2 bg-info text-center text-white d-block">
                        Active
                    </span>

                    <span v-else-if="data.row.StatusName == 'Inactive'"
                        class="px-2 bg-danger text-center text-white d-block">
                        Inactive
                    </span>

                    <span v-else-if="data.row.StatusName == 'RequisitionCompleted'"
                        class="px-2 bg-success text-center text-white d-block">
                        Assigned
                    </span>

                    <span
                        v-else-if="data.row.StatusName == 'Asset Requisition Cancelled By Assigner' || 'Asset Requisition Cancelled By User'"
                        class="px-2 bg-danger text-center text-white d-block">
                        Rejected
                    </span>

                </template>
                <template v-if="data.column.COLUMN_NAME == 'Reject'">
                    <span v-if="data.row.StatusName == 'Asset Requisition Cancelled By Assigner'">
                    </span>
                    <span v-else class="text-center single-action">
                        <a href="javascript:;" class="Cancel" @click="rejectReason(data.row)" title=""
                            data-toggle-tooltip="tooltip" data-original-title="Cancel">
                            <em class="fa fa-times-circle action-icon text-danger" title="Cancel"></em>
                        </a>
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'CreatedAt'">
                    <span :title="$options.filters.utcTimezoneToUserTimeZone(data.row.CreatedAt, Timezone)">{{
            $options.filters.utcTimezoneToUserTimeZone(data.row.CreatedAt, Timezone) }}</span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'FromTime'">
                    <span :title="$options.filters.formatDateTimeMMDDYY(data.row.FromTime)">{{
            $options.filters.formatDateTimeMMDDYY(data.row.FromTime) }}</span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'ToTime'">
                    <span :title="$options.filters.formatDateTimeMMDDYY(data.row.ToTime)">{{
            $options.filters.formatDateTimeMMDDYY(data.row.ToTime) }}</span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'ModifiedAt'">
                    <span ::title="$options.filters.utcTimezoneToUserTimeZone(data.row.ModifiedAt, Timezone)">{{
            $options.filters.utcTimezoneToUserTimeZone(data.row.ModifiedAt, Timezone) }}</span>
                </template>



                <template v-if="data.column.COLUMN_NAME == 'Assigned'">
                    <span v-if="data.row.IsAssigned">Yes</span>
                    <span v-else-if="data.row.StatusName == 'Asset Requisition Cancelled By Assigner'">Unassigned
                    </span>
                    <span v-else>Pending</span>
                </template>
            </template>
        </tg-list>

        <Reject v-if="isRejectPopup" :specificDetails="specificDetails" @closeRejectPopUp="closeRejectPopUp"></Reject>

    </section>
</template>
<script>
import DataService from '../../service/DataService'
import Schema from "../AssetRequisition/MyRequisitionsListSchema"
import Reject from "../../components/Modal/RejectReason.vue";
export default {

    components: {
        Reject
    },
    data() {
        return {
            isRejectPopup: false,
            isLoading: false,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            LegendArray: [],
            ProductData: [],
            widgets: [],
            Headers: [{
                "COLUMN_NAME": "AssetGroup",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Products",
                "DISPLAY_ORDER": 1,

            },
            {
                "COLUMN_NAME": "Assigned",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Assigned",
                "DISPLAY_ORDER": 2,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "UserName",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Created By",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "CreatedAt",
                "DATA_TYPE": "Date",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Created At",
                "DISPLAY_ORDER": 4,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "FromTime",
                "DATA_TYPE": "Date",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "From",
                "DISPLAY_ORDER": 5,
                settings: {
                    isInSlot: true
                }
            },

            {
                "COLUMN_NAME": "ToTime",
                "DATA_TYPE": "Date",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "To",
                "DISPLAY_ORDER": 6,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "ModifyUserName",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Approve/Rejected By",
                "DISPLAY_ORDER": 7
            },
            {
                "COLUMN_NAME": "ModifiedAt",
                "DATA_TYPE": "Date",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Approve/Rejected at",
                "DISPLAY_ORDER": 8,
                settings: {
                    isInSlot: true
                }

            },
            {
                "COLUMN_NAME": "Comment",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Reason",
                "DISPLAY_ORDER": 9,


            },
            {
                "COLUMN_NAME": "Status",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 10,
                settings: {
                    isInSlot: true
                }

            },
            {
                "COLUMN_NAME": "Reject",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Reject",
                "DISPLAY_ORDER": 10,
                settings: {
                    isInSlot: true
                }

            }
            ],
            specificDetails: {

            },
            noRecord: true,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            SortBy: '',
            SortExp: '',
            PageSize: 10,
            PageNumber: 1,
            searchCondition: '',
            catalogId: null,
            uniqueNames: null,
            IsAddCheckOutRequestList: false,
        }
    },
    async created() {
        var vm = this;
        vm.Timezone = vm.GetUserInfo.TimeZone;
        console.log("user", vm.Timezone);
        vm.leftSearchFields[0].onChange = vm.getClickEvent;

        await vm.GetPageSize();
        Schema.bindleftCommonSearchdropdown(vm, 'AssetRequisition', 'Product Name');

        vm.FetchData();
        vm.LegendArray = [
            {
                class: 'legends_profile colorboxsquare',
                text: ('Expired Check-Out')
            },
        ]
    },
    methods: {

        async GetPageSize() {
            var vm = this;
            var params = 'key=DefaultPageSize'
            await DataService.GetConfigurationValueForCompanyByKey(params).then((response) => {
                if (response.data != null) {
                    vm.PageSize = parseInt(response.data);
                }
            });
        },
        getClickEvent: function () {
            //  this.leftSearchFields[1].listOptions = [];
            var assetCatId = this.leftSearchFields[0].value;

            Schema.bindleftCommonSearchdropdown(this, 'AssetRequisition', 'Unique Name', assetCatId);
        },
        FetchData: function () {
            var vueObj = this;
            vueObj.isLoading = true;
            var params = `LocationId=&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&${vueObj.searchCondition}`
            DataService.GetMyRequisitions(params).then(response => {
                vueObj.isLoading = false;
                vueObj.leftSearchFields[1].listOptions = [];

                if (response.data != null) {
                    if (response.data.length > 0) {
                        vueObj.ProductData = response.data;
                        vueObj.ProductData.forEach(row => {
                            row.additionalClass = '';
                            if (row.IsExpired == true) {
                                row.additionalClass = "danger-bar";
                            }
                        })
                        vueObj.TotalRecords = response.data[0].TotalRecord;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.noRecord = false;
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.ProductData = [];
                        vueObj.TotalRecords = 0
                        vueObj.isLoading = false;
                    }
                } else {
                    vueObj.noRecord = true;
                    vueObj.ProductData = [];
                    vueObj.TotalRecords = 0
                    vueObj.isLoading = false;
                }

            });
        },

        rejectReason: function (item) {
            var vm = this;
            vm.confirmR(vm.$t('Are you sure you want to reject the requisition?'), true, false, function (result) {
                vm.specificDetails = item;
                vm.isRejectPopup = true;

            });

        },

        closeRejectPopUp: function () {
            var vm = this;
            vm.specificDetails = {}
            vm.isRejectPopup = false;
            vm.FetchData();
        },

        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "AddCheckOut":
                    this.AddCheckOutRequest();
                    break;
            }
        },
        AddCheckOutRequest: function () {
            this.$router.push({ name: "AssetAssignmentCheckOut" });

        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        sortdata: function (s) {
            debugger
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },

        renderActions() { },

        GetUniqueNamesWithId(CatalogId) {
            var vm = this;
            vm.Schema.leftSearchFields[0].listOptions = [];
            var url = `assetCatalogId=${CatalogId}&name=&issueTo=&locationId=&requestFrom=ALL&assetType=`;
            DataService.GetUniqueAssetItems(url).then((response) => {
                response.data.forEach(element => {
                    let obj = {
                        name: element.Name,
                        value: element.Value
                    };
                    vm.Schema.leftSearchFields[0].listOptions.push(obj);
                });
            });
        }
    }
}

</script>